<template>
    <h2>{{getSocio.nombre}} {{getSocio.apellidos}} -------- $ {{getTotal}}</h2>
  <table class='table table-striped table-hover'>
            <thead>
                <tr>
                    <th>Fecha</th>
                    <th>Descripcion</th>
                    <th>Monto</th>
                    <th>Descargar</th>
                </tr>     
            </thead>
            <tbody>
                <tr v-for="fac in getFacturas" :key="fac._id">
                    <td>{{fac.fecha.slice(0,10)}}</td>
                    <td>{{fac.descripcion}}</td>
                    <td>{{fac.monto}}</td> 
                    <td>
                        <button
                            class="btn btn-info"
                            @click="generarPdf(fac)"
                        >
                            Descargar
                        </button>
                    </td>
                                    
                </tr>
            </tbody>
        
        </table>
        <button 
            class="btn btn-success"
            @click="setSalir"
        >
            Salir
        </button>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
    computed:{
        ...mapGetters('cuenta',['getFacturas','getSocio', 'getTotal']),
        ...mapMutations('cuenta',['setSalir'])
    },
    methods:{
        ...mapActions('cuenta',['generarPdf'])
    }
}
</script>

